import React, { useState } from 'react';
import { useForm } from '@formspree/react';
import './VehicleRequest.css';

const VehicleRequest = () => {
  const [state, handleSubmit] = useForm("mdkeaovp");
  const [selectedBrand, setSelectedBrand] = useState('');
  const [isCustomBrand, setIsCustomBrand] = useState(false);
  const [isCustomModel, setIsCustomModel] = useState(false);
  
  const vehicleTypes = [
    "Sedan", "SUV", "Crossover", "Hatchback", "Wagon", 
    "Pickup Truck", "Van", "Minivan", "Coupe", "Convertible"
  ];
  
  const manufacturers = [
    "Toyota", "Honda", "Nissan", "Mazda", "Subaru", 
    "Mitsubishi", "Lexus", "BMW", "Mercedes-Benz", "Audi",
    "Ford", "Chevrolet", "Hyundai", "Kia", "Volkswagen", 
    "Suzuki", "Other"
  ];

  const modelsByBrand = {
    Toyota: ["Camry", "Corolla", "RAV4", "Highlander", "4Runner", "Prius", "Vitz"],
    Honda: ["Civic", "Accord", "CR-V", "HR-V", "Pilot", "Fit"],
    Nissan: ["Altima", "Sentra", "Rogue", "X-Trail", "Murano", "Note"],
    // Add more models for other brands as needed
  };

  const years = Array.from({length: 26}, (_, i) => 2025 - i);

  const handleReset = () => {
    setSelectedBrand('');
    setIsCustomBrand(false);
    setIsCustomModel(false);
    document.querySelector('.vehicle-request-form').reset();
  };

  if (state.succeeded) {
    return (
      <div className="vehicle-request-page">
        <div className="vehicle-request-hero">
          <div className="container">
            <h1>Vehicle Request</h1>
            <p>Your request has been submitted successfully!</p>
          </div>
        </div>
        <section className="section">
          <div className="container">
            <div className="success-message">
              <p>Thank you for your vehicle request! We will review your preferences and contact you soon with available options.</p>
            </div>
          </div>
        </section>
      </div>
    );
  }

  const handleBrandChange = (e) => {
    const value = e.target.value;
    setSelectedBrand(value);
    setIsCustomBrand(value === 'Other');
    setIsCustomModel(false);
  };

  return (
    <div className="vehicle-request-page">
      <div className="vehicle-request-hero">
        <div className="container">
          <h1>Request a Vehicle</h1>
          <p>Tell us about your dream vehicle and we'll help you import it</p>
        </div>
      </div>
      
      <section className="section">
        <div className="container">
          <div className="form-container">
            <h2>Vehicle Preferences</h2>
            <form className="vehicle-request-form" onSubmit={handleSubmit}>
              <div className="form-grid">
                <div className="form-group">
                  <label htmlFor="vehicleType">Vehicle Type</label>
                  <select id="vehicleType" name="vehicleType" required>
                    <option value="">Select Vehicle Type</option>
                    {vehicleTypes.map(type => (
                      <option key={type} value={type}>{type}</option>
                    ))}
                  </select>
                </div>

                <div className="form-group">
                  <label htmlFor="manufacturer">Manufacturer/Brand</label>
                  {isCustomBrand ? (
                    <input
                      type="text"
                      id="manufacturer"
                      name="manufacturer"
                      placeholder="Enter manufacturer name"
                      required
                    />
                  ) : (
                    <select 
                      id="manufacturer" 
                      name="manufacturer" 
                      required
                      onChange={handleBrandChange}
                    >
                      <option value="">Select Manufacturer</option>
                      {manufacturers.map(brand => (
                        <option key={brand} value={brand}>{brand}</option>
                      ))}
                    </select>
                  )}
                </div>

                <div className="form-group">
                  <label htmlFor="model">Model</label>
                  {isCustomBrand || isCustomModel ? (
                    <input
                      type="text"
                      id="model"
                      name="model"
                      placeholder="Enter model name"
                      required
                    />
                  ) : (
                    <select 
                      id="model" 
                      name="model" 
                      required
                      onChange={(e) => setIsCustomModel(e.target.value === 'Other')}
                    >
                      <option value="">Select Model</option>
                      {selectedBrand && modelsByBrand[selectedBrand]?.map(model => (
                        <option key={model} value={model}>{model}</option>
                      ))}
                      {selectedBrand && <option value="Other">Other</option>}
                    </select>
                  )}
                </div>

                <div className="form-group">
                  <label htmlFor="year">Year</label>
                  <select id="year" name="year" required>
                    <option value="">Select Year</option>
                    {years.map(year => (
                      <option key={year} value={year}>{year}</option>
                    ))}
                  </select>
                </div>

                <div className="form-group">
                  <label htmlFor="color">Preferred Color</label>
                  <input
                    type="text"
                    id="color"
                    name="color"
                    placeholder="e.g. Black, White, Silver"
                    required
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="transmission">Transmission Type</label>
                  <select id="transmission" name="transmission" required>
                    <option value="">Select Transmission</option>
                    <option value="automatic">Automatic</option>
                    <option value="manual">Manual</option>
                  </select>
                </div>

                <div className="form-group">
                  <label htmlFor="fuelType">Fuel Type</label>
                  <select id="fuelType" name="fuelType" required>
                    <option value="">Select Fuel Type</option>
                    <option value="gas">Gas</option>
                    <option value="hybrid">Hybrid</option>
                    <option value="electric">Electric</option>
                  </select>
                </div>
              </div>

              <div className="form-group">
                <label htmlFor="additionalNotes">Additional Notes</label>
                <textarea
                  id="additionalNotes"
                  name="additionalNotes"
                  rows="4"
                  placeholder="Any specific requirements or preferences..."
                ></textarea>
              </div>

              <div className="button-group">
                <button type="submit" className="btn submit-btn" disabled={state.submitting}>
                  {state.submitting ? 'Submitting...' : 'Submit Request'}
                </button>
                <button 
                  type="button" 
                  className="btn reset-btn" 
                  onClick={handleReset}
                >
                  Reset Form
                </button>
              </div>
            </form>
          </div>
        </div>
      </section>
    </div>
  );
};

export default VehicleRequest;
