import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Navbar.css';
import logo from '../assets/images/logo.png';

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleNav = () => {
    setIsOpen(!isOpen);
  };

  return (
    <nav className="navbar">
      <div className="container navbar-container">
        <Link to="/" className="navbar-logo">
          <img src={logo} alt="SLR Imports Logo" />
          <div className="brand-text">
            <h1>SLR Imports Ltd</h1>
            <p>Life is Movement, We Keep You Moving</p>
          </div>
        </Link>
        
        <div className="menu-icon" onClick={toggleNav}>
          <div className={isOpen ? 'hamburger open' : 'hamburger'}></div>
        </div>
        
        <ul className={isOpen ? 'nav-menu active' : 'nav-menu'}>
          <li className="nav-item">
            <Link to="/" className="nav-link" onClick={() => setIsOpen(false)}>Home</Link>
          </li>
          <li className="nav-item">
            <Link to="/vehicle-imports" className="nav-link" onClick={() => setIsOpen(false)}>Vehicle Imports</Link>
          </li>
          <li className="nav-item">
            <Link to="/vehicle-request" className="nav-link" onClick={() => setIsOpen(false)}>Vehicle Request</Link>
          </li>
          <li className="nav-item">
            <Link to="/about" className="nav-link" onClick={() => setIsOpen(false)}>About Us</Link>
          </li>
          <li className="nav-item">
            <Link to="/contact" className="nav-link" onClick={() => setIsOpen(false)}>Contact</Link>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
